<template>
  <div class="section">
    <div class="tabs">
      <div :class="{active:tab == 1}" @click="tabs(1)">列表查看</div>
      <div :class="{active:tab == 2}" @click="tabs(2)">图形查看</div>
    </div>
    <Lay v-show="tab==1" :route="route" :arr="arr" :url="url" />
    <div v-show="tab==2" class="main2" id="char2"></div>
  </div>
</template>

<script>
  import Lay from "@/components/common/template";
  import request from "@/utils/request";

  import {
    welldepth
  } from "@/config/well";
  import echarts from "echarts";
  import e3d from "echarts-gl";
  export default {
    props: ["route"],
    data() {
      return {
        url: "/data/query/wellstructuredata",
        arr: welldepth,
        tab: 2,
        char: {},
        canvas: {},
        ctx: {}
      };
    },

    components: {
      Lay
    },

    computed: {},

    mounted: function () {
      let data = this.route;
      console.log(data);
      this.char = echarts.init(document.getElementById('char2'));

      request({
        url: '/data/query/trajectoryprofiledata', //请求井眼轨迹数据
        method: 'post',
        data
      }).then(res => {
        // console.log(res);
        let rs = [];
        res.forEach(item => {
          if (item.inc > 3 && item.inc < 5) {
            rs.push([item.depth, item.close_dis]);
            console.log(rs);
            let yy =rs[0].slice(-1)
            console.log(yy);

          }
        });
      });

      request({
        url: '/data/query/targetdata', //请求定向井数据
        method: 'post',
        data
      }).then(res => {
        // console.log(res);
        let fs_a = [];
        let fs_b = [];
        res.forEach(item => {
          if (item.target == "A") {
            // console.log(item);
            fs_a.push([item.x_axis, item.y_axis]);
            // console.log(fs_a);
            fs_a.pop();
            // console.log(fs_a);
          } else if (item.target == "B") {
            // console.log(item);
            fs_b.push([item.x_axis, item.y_axis]);
          }
        })
      });
    },
    methods: {
      tabs(i) {
        if (this.tab == i) return;
        this.tab = i;
        if (i == 2) {
          // this.draw2();
        }
      },
      setData(data) {
        this.char.setOption({
          xAxis: {
            name: '闭合距',
            type: 'category',
            data: ['10', '20', '30', '40', '50', '60', '70']
          },
          yAxis: {
            name: '井深',
            type: 'value'
          },
          series: [{
            data: [820, 932, 901, 934, 1290, 1330, 1320],
            type: 'line',
            smooth: true
          }]
        })
      }
    }
  };

</script>
<style lang="scss" scoped>
  #char2 {
    width: 100%;
    height: 100%;
    background-color: #fff;
  }

</style>



